@import './mixins.scss';
@import './variables.scss';


.header {
    scroll-snap-align: start;
    height: 100vh;
    background-color: $gray;
    @include flex_center;
    font-family: $font-generic;
    position: relative;

    &_line1 {
        background-color: $green;
        height: 2px;
        width: 90%;
        position: absolute;
        top: 10%;
    }


    &_line2 {
        background-color: $green;
        width: 2px;
        position: absolute;
        top: 10%;
        right: 5%;
        height: 80%;
    }

    &_container {
        width: 80vw;
        height: 70vh;
        background-color: $black;
        @include flex_center;
        flex-direction: column;
        flex-wrap: wrap;

        &_titles {
            min-width: 100%;
            @include flex_center;

            &_title {
                color: white;
                @include title_header;
            }

            &_title2 {
                color: $green;
                @include title_header;
            }
        }

        &_subtitle {
            @include title_section;
            margin-top: 3rem;
            color: white;
            display: block;
            font-weight: 100;
        }
    }
}

.construction {
    font-weight: 400;
    color: red;
}